.wp-block-create-block-rika-team-members {
	display: block;

	@for $i from 1 through 6 {
		&.has-#{$i}-columns {
			/* stylelint-disable-next-line selector-class-pattern */
			> .block-editor-inner-blocks > .block-editor-block-list__layout > {

				[data-type="blocks-course/team-member"] {
					width: calc(100% / #{$i} - 20px);
					.wp-block-blocks-course-team-member-img {
						position: relative;
		
						&.is-loading img {
							opacity: 0.3;
						}
		
						.components-spinner {
							position: absolute;
							left: 0;
							right: 0;
							top: 0;
							bottom: 0;
							margin: auto;
						}
					}
				}

				.block-list-appender {
					align-self: stretch;
					width: calc(100% / #{$i} - 20px);
					border: 1px dashed;
					display: flex;
					align-items: center;
					justify-content: center;
					min-height: 70px;
				}
			}
		}
	}
	/* stylelint-disable-next-line selector-class-pattern */
	> .block-editor-inner-blocks > .block-editor-block-list__layout {
		display: flex;
		flex-wrap: wrap;
		margin-left: -10px;
		margin-right: -10px;

		[data-type="create-block/team-member"] {
			max-width: none;
			margin: 0;
		}

		> [data-type="create-block/team-member"] {
			margin: 10px;

			h4 {
				color: #222;
				font-size: 22px;
				font-size: bold;
				margin: 0;
			}

			p {
				color: #565656;
				font-size: 16px;
				font-weight: 300;
				margin-bottom: 5px;
				margin-top: 5px;
			}
		}
	}
}
.wp-block-course-social-links li {
    display: inline-block;
    list-style: none;
    margin: 0 8px;
}

.wp-block-course-social-links li button {
    background: transparent;
    border: 1px dotted;
    height: 35px;
	cursor: pointer;
    width: 35px;
}
.wp-block-course-social-links li.is-selected {
    border: 2px solid #007cba;
}
.wp-block-course-social-links ul li {
    display: inline-block;
    list-style: none;
    margin-right: 10px;
}